import { MenuFoldOutlined, QuestionCircleTwoTone, SearchOutlined } from "@ant-design/icons";
import { Button, Col, DatePicker, Drawer, Form, PageHeader, Popover, Row, Select, Tabs, Tooltip } from "antd";
import LocationPicker from "components/shared-components/LocationPicker";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import TableMenuItem from "components/shared-components/TableMenuItem";
import {
  CHILDREN,
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  REPORTS_DESCRIPTION,
  SEARCH,
  SELECT_METHODS,
  SELECT_TEXT,
  SORT_DIRECTIONS,
} from "constants/CommonConstants";
import { MENU_ITEMS, MENU_ITEMS_TYPES } from "constants/EmployeeLoginDetails";
import useIntlMessage from "hooks/useIntlMessage";
import _ from "lodash";
import moment from "moment";
import { useEffect, useMemo, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setBenchReserveReportColumns } from "redux/actions";
import { showLoader } from "redux/actions/Loader";
import benchReportService from "services/BenchReportService";
import Utils from "utils";
import FileHandler from "utils/fileHandler";
import {
  DATA_TABLE_COLUMNS,
  EMPLOYEE_TYPE,
  FILE_NAME,
  FORM_NAME,
  MONTHS_FILTER_DROPDOWN,
} from "../../../../constants/BenchOrReservedConstant";
import { FILTER_FORM_FIELDS } from "../../../../constants/CommonConstants";
import BenchReportSummary from "./BenchReportSummary";
import BenchReportVisual from "./BenchReportVisual";

const ALL = "All";

const { Option } = Select;

const defaultSearchParams = {
  fromDate: moment().startOf("month"),
  toDate: moment(new Date()),
};

const BenchReport = () => {
  useEffect(() => {
    return () => {
      dispatch(showLoader(false));
    };
  }, []);
  const dispatch = useDispatch();

  const getMessage = useIntlMessage();
  const [open, setOpen] = useState(false);
  const [filteredBenchReportData, setFilteredBenchReportData] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({});
  const [openPopOver, setOpenPopOver] = useState(false);
  const [loading, setLoading] = useState(false);
  const [visibility, setVisibility] = useState();
  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const [startDate, setStartDate] = useState();
  const [searchParams, setSearchParams] = useState(defaultSearchParams);
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const accountsList = useSelector(store => store.myTeams.accountsList);
  const [activeTabKey, setActiveTabKey] = useState("report");
  const [reportParams, setReportParams] = useState();
  const [graphParams, setGraphParams] = useState();
  const [summaryParams, setSummaryParams] = useState();
  const [summaryData, setSummaryData] = useState([]);
  const [graphData, setGraphData] = useState([]);
  const [quickActionBtns, setQuickActionBtns] = useState([]);
  const [dynamicColumns, setDynamicColumns] = useState([]);
  const [initialVisibleColumns, setInitialVisibleColumns] = useState([]);
  const [filteredVisibleData, setFilteredVisibleData] = useState([]);
  const visibleFilteredColumns = useSelector(state => state.reports?.benchReserveReportColumns);

  let searchInput = useRef(null);
  let currentVisibleTableData = [];
  const [form] = Form.useForm();
  const getColumnSearchProps = field => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <SearchFilterDropdown
        setSelectedKeys={setSelectedKeys}
        selectedKeys={selectedKeys}
        confirm={confirm}
        clearFilters={clearFilters}
        handleSearch={handleSearch}
        dataIndex={field.key}
        searchInput={searchInput}
        field={field}
        handleReset={handleReset}
        label={field.title}
        dataSource={filteredBenchReportData}
      />
    ),
    filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />,
    onFilter: (value, record) => {
      if (field.type === "date") {
        const valuee = value.format(DATE_FORMAT).toString();
        const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
        return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
      }
      return record[field.key]?.toString().toLowerCase().includes(value.toLowerCase());
    },
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput && searchInput.select && searchInput.select());
      }
    },
  });

  useEffect(() => {
    if (
      searchParams &&
      searchParams.hasOwnProperty("accounts", "locations", "benchStatus", "benchAgingFilter", "fromDate", "toDate")
    ) {
      loadData(searchParams);
    }
  }, [activeTabKey]);

  const loadData = params => {
    switch (activeTabKey) {
      case "report":
        if (!_.isEqual(params, reportParams)) {
          fetchBenchReport(params);
          setReportParams(params);
        }
        break;
      case "summary":
        if (!_.isEqual(params, summaryParams)) {
          fetchSummary(params);
          setSummaryParams(params);
        }
        break;
      case "visual":
        if (!_.isEqual(params, graphParams)) {
          fetchGraphs(params);
          setGraphParams(params);
        }
        break;
    }
  };

  const fetchSummary = async params => {
    try {
      dispatch(showLoader(true));
      const response = await benchReportService.getReportSummary(params);
      if (response) {
        setSummaryData(response);
      }
    } catch (e) {
      console.error(e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const fetchGraphs = async params => {
    try {
      dispatch(showLoader(true));
      const response = await benchReportService.getReportGraphs(params);
      if (response) {
        setGraphData(response);
      }
    } catch (e) {
      console.error(e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const generateDynamicColumns = columns => {
    return columns.map(field => {
      return {
        title: (
          <Tooltip title={field.title} placement="top">
            <span>{field.title}</span>
          </Tooltip>
        ),
        dataIndex: field.key,
        key: field.key,
        sorter:
          field.sortable &&
          (field.key === DATA_TABLE_COLUMNS.ALLOCATION_AGING.key
            ? null
            : (a, b) => Utils.antdTableSorter(a, b, field.key)),
        sortDirections: SORT_DIRECTIONS,
        isVisible: visibility[field.key],
        ...(field.searchable ? getColumnSearchProps(field) : null),
        render: (_, record) => {
          if (field.type === "date") {
            return record[field.key] && moment(record[field.key]).format(DATE_FORMAT);
          }
          return (
            <Tooltip title={record[field.key]} placement="top">
              <span id={field.key}>{record[field.key]}</span>
            </Tooltip>
          );
        },
      };
    });
  };
  const filteredColumns = useMemo(
    () => generateDynamicColumns(dynamicColumns).filter(item => item.isVisible),
    [visibility, loading],
  );

  const handleChange = (pagination, filters, sorter, extra) => {
    setFilteredVisibleData(extra.currentDataSource);
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleSearch = (selectedKeys, search, dataIndex) => {
    search();
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    confirm();
  };

  const onSearch = async values => {
    if (!Utils.isEmpty(values)) {
      const { status, month, fromDate, toDate, city, account } = values;
      if (!Utils.validateEndDate(fromDate, toDate)) {
        const payload = {
          fromDate: fromDate,
          toDate: toDate,
          locations: city,
          accounts: account,
          benchStatuses: status,
          benchAgingFilters: month,
        };
        setSearchParams(payload);
        loadData(payload);
      }
    }
  };

  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const onColHide = data => {
    const _visibility = { ...visibility, [data.key]: !visibility[data.key] };
    dispatch(setBenchReserveReportColumns(_visibility));
    setVisibility({ ..._visibility });
  };

  const clearFilters = () => {
    // setFilteredInfo({});
    // setSortedInfo({});
    setSearchParams(defaultSearchParams);
    setVisibility(initialVisibleColumns);
    form.resetFields();
  };

  const onMenuItemClick = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.clearAllFilters:
        clearFilters();
        break;
      case MENU_ITEMS_TYPES.exportAllExcel:
      case MENU_ITEMS_TYPES.exportVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllPDF:
      case MENU_ITEMS_TYPES.exportVisiblePDF:
        exportToPDF(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisiblePDF:
        exportToPDF(key);
        break;
      default:
        return;
    }
  };

  const exportToExcel = type => {
    Utils.exportToExcel({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: filteredBenchReportData,
      visibleReportData: currentVisibleTableData,
      visibility: visibility,
      fileName: FILE_NAME,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : filteredBenchReportData,
    });
    setIsMenuVisible(false);
  };

  const exportToPDF = type => {
    Utils.exportToPDF({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: filteredBenchReportData,
      visibleReportData: currentVisibleTableData,
      visibleReportColumns: filteredColumns,
      tableDynamicColumns: generateDynamicColumns(dynamicColumns),
      visibility: visibility,
      fileName: FILE_NAME,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : filteredBenchReportData,
    });
    setIsMenuVisible(false);
  };

  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  const fetchBenchReport = async payload => {
    try {
      setLoading(true);
      dispatch(showLoader(true));

      const response = await benchReportService.getReport(payload);
      if (response) {
        const { columns, data, quickActions } = response;
        if (!!columns.length) {
          if (!Utils.isEmpty(visibleFilteredColumns)) {
            setVisibility(visibleFilteredColumns);
            setInitialVisibleColumns(visibleFilteredColumns);
            setDynamicColumns(columns);
          } else {
            let initailColumns = Utils.createObjFromArray(columns);
            setVisibility(initailColumns);
            setInitialVisibleColumns(initailColumns);
            setDynamicColumns(columns);
          }
        }
        setFilteredBenchReportData(data);
        setQuickActionBtns(quickActions);
      }
    } catch (e) {
      console.error(e);
    } finally {
      setLoading(false);
      dispatch(showLoader(false));
    }
  };
  const onChangeStartDate = e => {
    setStartDate(e);
  };
  const disableBeforeDate = current => {
    return Utils.disableBeforeAfterDate(current, form.getFieldValue(FILTER_FORM_FIELDS.START_DATE.name));
  };
  const handleOpenChange = newOpen => {
    setOpenPopOver(newOpen);
  };

  return (
    <>
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={12} md={12} lg={24} xl={24}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("sidenav.reports.bench")}
            subTitle={
              <Popover
                placement="right"
                trigger="click"
                content={REPORTS_DESCRIPTION.benchReservedReport}
                open={openPopOver}
                onOpenChange={handleOpenChange}
              >
                <QuestionCircleTwoTone className="font-size-md mb-0" />
              </Popover>
            }
          />
        </Col>
      </Row>
      <Form
        form={form}
        name={FORM_NAME}
        onFinish={onSearch}
        initialValues={{
          fromDate: moment().startOf("month"),
          toDate: moment(new Date()),
        }}
        layout="horizontal"
        wrapperCol={{ span: 16 }}
        labelCol={{ span: 8 }}
        labelAlign="left"
      >
        {!filteredBenchReportData ? null : (
          <Row justify="space-between" gutter={[20, 20]} className="mt-4">
            <Col lg={24} md={24}>
              <div className="ant-affix mt-2 menu-bar">
                <Button type="primary" onClick={showDrawer} shape="circle" className="mr-3">
                  <MenuFoldOutlined />
                </Button>
              </div>
            </Col>
          </Row>
        )}
        <Row className="w-100" gutter={[20, 5]}>
          <Col lg={8} md={12} xs={24}>
            <Form.Item
              label={getMessage(FILTER_FORM_FIELDS.START_DATE.label)}
              name={FILTER_FORM_FIELDS.START_DATE.name}
              rules={[{ required: true }]}
            >
              <DatePicker
                className={"w-100"}
                format={DATE_FORMAT}
                disabledDate={Utils.disableFutureDates}
                onChange={onChangeStartDate}
              />
            </Form.Item>
          </Col>
          <Col lg={8} md={12} xs={24}>
            <Form.Item
              label={getMessage(FILTER_FORM_FIELDS.END_DATE.label)}
              name={FILTER_FORM_FIELDS.END_DATE.name}
              rules={[{ required: true }]}
            >
              <DatePicker
                className={"w-100"}
                format={DATE_FORMAT}
                disabledDate={disableBeforeDate}
                onSelect={value => setStartDate(value)}
              />
            </Form.Item>
          </Col>
          <Col lg={8} md={12} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.MONTHS.name}
              label={getMessage(FILTER_FORM_FIELDS.MONTHS.text)}
              rules={[{ required: true }]}
            >
              <Select
                showSearch
                optionFilterProp={CHILDREN}
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.MONTHS.text)}
                mode={SELECT_METHODS.MULTIPLE}
                onSelect={value => Utils.onMultiSelection(value, form, FILTER_FORM_FIELDS.MONTHS.name)}
              >
                <Option data-test-id="bench-report-type-all" value="-1">
                  {ALL}
                </Option>
                {MONTHS_FILTER_DROPDOWN &&
                  MONTHS_FILTER_DROPDOWN?.map((x, index) => (
                    <Option key={index} value={x.value}>
                      {x.name}
                    </Option>
                  ))}
              </Select>
            </Form.Item>
          </Col>
          <Col lg={8} md={12} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.STATUS.name}
              label={getMessage(FILTER_FORM_FIELDS.STATUS.text)}
              rules={[{ required: true }]}
            >
              <Select
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.STATUS.text)}
                mode={SELECT_METHODS.MULTIPLE}
                optionFilterProp={CHILDREN}
                onSelect={value => Utils.onMultiSelection(value, form, FILTER_FORM_FIELDS.STATUS.name)}
              >
                <Option data-test-id="bench-report-type-all-statuses" value={ALL}>
                  {ALL}
                </Option>
                {EMPLOYEE_TYPE &&
                  EMPLOYEE_TYPE.map((x, index) => (
                    <Option key={index} value={x.value}>
                      {x.name}
                    </Option>
                  ))}
              </Select>
            </Form.Item>
          </Col>
          <Col lg={8} md={12} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.CITY.name}
              label={getMessage(FILTER_FORM_FIELDS.CITY.label)}
              rules={[{ required: true }]}
            >
              <LocationPicker
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.CITY.label)}
                locations={allLocations}
                name={FILTER_FORM_FIELDS.CITY.name}
                form={form}
              />
            </Form.Item>
          </Col>

          <Col lg={8} md={12} xs={24}>
            <Form.Item
              name={FILTER_FORM_FIELDS.ACCOUNT.name}
              label={getMessage(FILTER_FORM_FIELDS.ACCOUNT.label)}
              rules={[{ required: true }]}
            >
              <Select
                allowClear
                showSearch
                optionFilterProp={CHILDREN}
                mode={SELECT_METHODS.MULTIPLE}
                placeholder={getMessage(SELECT_TEXT.label, FILTER_FORM_FIELDS.ACCOUNT.label)}
                onSelect={value => Utils.onAccountSelection(value, form, FILTER_FORM_FIELDS.ACCOUNT.name)}
              >
                <Option value={ALL}>{ALL}</Option>
                {accountsList?.map((account, index) => (
                  <Option key={index} value={account.accountName}>
                    {account.accountName}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={[5, 5]} className="mb-3">
          <Col lg={24} md={24} xs={24}>
            <Button type="primary" icon={<SearchOutlined className="mr-2" />} htmlType="submit" className="float-right">
              {SEARCH.text}
            </Button>
          </Col>
        </Row>
      </Form>
      {/* <Row className="mb-3">
        <Legend />
      </Row> */}

      <Tabs type="card" onChange={key => setActiveTabKey(key)}>
        <Tabs.TabPane tab="Report" key="report" className="pl-4 pr-3 pt-3" style={{ position: "relative" }}>
          {!!filteredBenchReportData.length && !!quickActionBtns.length && (
            <div className="is-mobile">
              <div className="floating-btn floating-buttons">
                {quickActionBtns.map(item => {
                  return (
                    <Tooltip title={item.description} placement="top">
                      <Button
                        type="primary"
                        className="floating-btn-circle my-1"
                        onClick={() => Utils.scrollIntoColumn(item.key)}
                      >
                        {item.title}
                      </Button>
                    </Tooltip>
                  );
                })}
              </div>
            </div>
          )}
          <TableComponent
            rowKey={(record, index) => `${record.employeeId}${index}`}
            columns={filteredColumns}
            data={filteredBenchReportData}
            scroll={{ x: true }}
            handleChange={handleChange}
            visibleData={data => {
              currentVisibleTableData = data;
            }}
            tableSize="small"
            visible={isMenuVisible}
            onDropdownMenuChange={handleMenuVisibleChange}
            showSorterTooltip={false}
            rowClassName={record =>
              record?.reservationExpired
                ? "table-row-highlight"
                : record?.nextAllocationStartDateAlert
                ? "table-row-highlight-1"
                : ""
            }
          />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Summary" key="summary" className="p-3">
          <BenchReportSummary summaryData={summaryData} />
        </Tabs.TabPane>
        <Tabs.TabPane className="p-3" tab="Visual" key="visual">
          <BenchReportVisual graphData={graphData} />
        </Tabs.TabPane>
      </Tabs>

      <Drawer onClose={onClose} visible={open} destroyOnClose width={250}>
        <TableMenuItem
          columns={generateDynamicColumns(dynamicColumns)}
          onColHide={onColHide}
          menuItems={MENU_ITEMS}
          onMenuItemClick={onMenuItemClick}
        />
      </Drawer>
    </>
  );
};

export default BenchReport;
